<template>
  <ul class="page-menu">
    <li v-for="page in pageList" :key="page.koreanName" @click="setPage(page)" 
      :class="{active:selectedPage.koreanName === page.koreanName}"
    >
      {{ page.koreanName }}
    </li>
  </ul>
</template>

<script setup>
  import {onMounted, ref, computed} from 'vue'
  import {useStore} from 'vuex'

  const store = useStore()
  const selectedPage = ref(computed(()=>store.getters.getSelectedPage))

  const pageList = [
    {
      component:'statusPage',
      koreanName:'능력치',
    },
    {
      component:'equipmentsPage',
      koreanName:'장비',
    },
    {
      component:'skillPage',
      koreanName:'스킬',
    },
    {
      component:'inventoryPage',
      koreanName:'인벤토리',
    },
    {
      component:'questPage',
      koreanName:'퀘스트',
    },
    {
      component:'statusPage',
      koreanName:'보스',
    },
    {
      component:'settingsPage',
      koreanName:'환경설정',
    },
    {
      component:'systemPage',
      koreanName:'시스템',
    },
  ]
  const setPage = (page) =>{
    store.commit('setSelectedPage', page)
  }
  onMounted(()=>{
    setPage(pageList[0])
  })
</script>

<style scoped>
  .page-menu{
    display:flex; 
    gap:26px;
    padding:14px;
  }
  .page-menu li{
    color:#fff;
    font-size:20px;
    font-weight:bold;
    padding-bottom:6px;
  }
  .page-menu li.active{
    color:#abed35;
    border-bottom:2px solid #abed35;
  }
</style>