<template>
  <div class="ui-box">
    <pageTab/>
    <component :is="currentPage"/>
  </div>
    
</template>

<script setup>

  import pageTab from '@/components/pageTab.vue'
  import {markRaw, ref, computed, onMounted, watch} from 'vue'
  import {useStore} from 'vuex'
  import questPage from '@/components/questPage.vue'
  import skillPage from '@/components/skillPage.vue'
  import systemPage from '@/components/systemPage.vue'
  import statusPage from '@/components/statusPage.vue'
  import settingsPage from '@/components/settingsPage.vue'
  import inventoryPage from '@/components/inventoryPage.vue'
  import equipmentsPage from '@/components/equipmentsPage.vue'
  const store = useStore()
  const currentPage = ref(null)
  const selectedPage = ref(computed(()=>store.getters.getSelectedPage))
  const compList = {
    questPage,
    skillPage,
    systemPage,
    statusPage,
    settingsPage,
    inventoryPage,
    equipmentsPage
  }
  watch(selectedPage, async ()=>{
    currentPage.value = await markRaw(compList[selectedPage.value.component])
  })
  onMounted(async ()=>{
    currentPage.value = await markRaw(compList[selectedPage.value.component])
  })
</script>

<style scoped>
  .ui-box{
    width:calc(100% - 200px);
    margin:40px auto; 
    background:rgba(0,0,0,0.8);
  }
</style>