<template>
  <div class="status-page">
    <div class="status-page__basics" v-if="charInfo.basic">
      <div class="status-page__char-img-box">
        <img class="status-page__char-img" :src="charImg" :alt="charInfo.basic.character_name">
      </div>
      <div class="status-page__basic-section">
        <div> {{charInfo.basic.character_name }} / LV. {{ charInfo.basic.character_level }} {{charInfo.basic.character_class}}</div>
        <div class="status-page__char-guild">
          길드 
          <span>{{ charInfo.basic.character_guild_name === null ? '-' : charInfo.basic.character_guild_name }}</span>
        </div>
        <div class="status-page__char-association">
          연합
          <span>{{ charInfo.basic.character_guild_name === null ? '-' : charInfo.basic.character_guild_name }}</span>
        </div>
        <div class="status-page__bars">
          <div class="status-page__exp">
            <h5>EXP {{charInfo.basic.character_exp}}</h5>
            <progress :value="charInfo.basic.character_exp_rate" max="100"></progress>
          </div>     
        </div>
      </div>      
    </div>
    <div class="status-page__wrapper">
      <div class="status-page__stat" :class="{active:selectedArea === 'stat'}" @click="setSelectedArea('stat')">
        <h1>STAT</h1>
        <div class="status-page__stat-section" v-for="(stat, i) in charInfo.stat" :key="stat" :id="`stat-section${i}`">
          <div class="status-page__left-stat">
            <dl v-for="leftStat in stat.left" :key="leftStat">
              <dt>{{ leftStat.stat_name}}</dt>
              <dd>{{ leftStat.stat_value}}</dd>
            </dl>
          </div>
          <div class="status-page__right-stat">
            <dl v-for="rightStat in stat.right" :key="rightStat">
              <dt>{{ rightStat.stat_name}}</dt>
              <dd>{{ rightStat.stat_value}}</dd>
            </dl>
          </div>
        </div>
      </div>
      <div class="status-page__hyper-stat" :class="{active:selectedArea === 'hyper-stat'}" @click="setSelectedArea('hyper-stat')">
        <h1>HYPER STAT</h1>
        <h2>남은 포인트 <span>{{charInfo.hyperStat.hyper_stat_preset_1_remain_point}}</span></h2>
        <div>
          <dl v-for="stat in charInfo.hyperStat.hyper_stat_preset_1" :key="stat">
            <dt>{{ stat.stat_type }}</dt>
            <dd>{{ stat.stat_level }}</dd>
          </dl>
        </div>
      </div>
      <div class="status-page__ability" :class="{active:selectedArea === 'ability'}" @click="setSelectedArea('ability')">
        <h1>AVILITY</h1>
      </div>
    </div>
    <div class="status-page__stat-info">
      스탯 설명
    </div>
  </div>
</template>

<script setup>
  import {onMounted, ref, computed} from 'vue' 
  import {useStore} from 'vuex'

  const store = useStore()
  const today = new Date()
  const twoDaysAgo = new Date(today.setDate(today.getDate() - 2));
  const dateString = new Intl.DateTimeFormat("fr-CA", {year: "numeric", month: "2-digit", day: "2-digit"}).format(twoDaysAgo)
  const ocid = ref(computed(()=>store.getters.getOCID))
  const charInfo = ref(computed(()=>store.getters.getCharInfo))
  const charImg = ref(null)
  const selectedArea = ref('stat')
  const setSelectedArea = (areaName) =>{
    selectedArea.value = areaName
  }
  onMounted(async ()=>{
    await store.dispatch('getOCID')
    await store.dispatch('getCharInfo', {ocid:ocid.value, date:dateString})
    charImg.value = charInfo.value.basic.character_image
  })
</script>

